.primary-nav {
  padding-top: 10px;

  @mixin atMedium {
    padding-top: 0;
  }
  
  ul {
    margin: 0;
    padding: 0;
    @mixin clearfix;
  }

  li {
    list-style: none;
    display: inline-block;
    padding-right: 7px;
    
    @mixin atMedium {
      padding-right: 20px;
      display: block;
      float: left;
    }
  }

  li:last-child {
    padding-right: 0;
    @mixin atMedium {
      padding-right: 20px;
    }
  }

  &--pull-right {
    @mixin atMedium {
      float:right;
    }
  }

  a {
    display: block;
    color: white;
    text-decoration: none;
    font-weight: 300;
    padding: 5px 8px;
    font-size: .8rem;
    background-color: rgba($mainBlue, .5);

    @mixin atMedium {
      font-size: 1rem;
      padding: 12px 0;
      background-color: transparent;
    }
  }
}