.large-hero {
  position: relative;
  border-bottom: 10px solid $mainBlue;

  &__text-content {
    padding-top: 88px;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;

    @mixin atMedium {
      padding-top: 60px;
    }
  }

  &__title {
    font-weight: 300;
    color: $mainBlue;
    font-size: 2.4rem;
    margin:0;

    @mixin atSmall {
      font-size: 4.8rem;
    }
  }

  &__subtitle {
    font-weight: 300;
    color: $mainBlue;
    font-size: 1.5rem;
    margin: 0;

    @mixin atSmall {
      font-size: 2.9rem;
    }
  }

  &__description {
    color: white;
    font-size: 1.1rem;
    font-weight: 100;
    text-shadow: 2px 2px 0 rgba(0, 0, 0, .1);
    max-width: 30rem;
    margin-left: auto;
    margin-right: auto;

    @mixin atSmall {
      font-size: 1.875rem;
    }
  }

  &__image {
    /* Image elements are inline elements, this causes a subscript gap for the text.
        Changing it to block removes that gap. */
    display: block;
  }
}